import React, { useState, useEffect } from 'react';
import Table from '../../../../../components/Tables';

import caretDown from '../../../../../assets/icons/caret-down.png';
import { useHistory } from 'react-router-dom';
import instances from '../../../../../axios/authbearer';

const DiscountPartnersTable = () => {
  const history = useHistory();
  //const [datas, setDatas] = useState();
  const [List, setList] = useState([]);
  const [avail, setAvail] = useState(false);
  async function fetchdata() {
   
      try {
        let res = await instances.get<any>(
          '/merchant/v2/admin/get-partners?platform=web&pending=true',
        );

        let result = await res.data;
        if (result && result.status === 'success') {
          //setDatas(result.data.Partners);
          let ar:any = [];
          result.data.Partners.forEach(item=>{
            let q = {
                id: item.user_id,
                Partners: item.business_name,
                Offer: item.max_discount,
                Category: item.business_category,
                Location: item.address+""+item.city,
                status: item.partner_approval==="approved"?"Approved":"Pending",
                all:"?phone="+item.contact_phone_number+"&email="+item.contact_email+"&location="+item.address+""+item.city+"&wallet="+item.wallet+"&logo="+item.logo+"&status="+item.partner_approval+"&name="+item.business_name
         
  
            };
            ar.push(q);
          })
          setList(ar);
        }
      } catch (e) {
        console.log(e);
      }
    

    
  }
  useEffect(() => {
    (async () => {
      if (avail === false) {
        fetchdata();
        setAvail(true);
      }
    })();
  });

  // const toggle = () => setPopoverOpen2(!popoverOpen2);

  const tableHead: { name: keyof TableData; displayName: any }[] = [
    {
      name: 'Partners',
      displayName: (
        <div className="d-flex align-items-center">
          <span className="mr-1">Partners</span>
          <img src={caretDown} alt={caretDown} />
        </div>
      ),
    },
    { name: 'Offer', displayName: 'Offer' },
    { name: 'Category', displayName: 'Category' },
    { name: 'Location', displayName: 'Location' },
    { name: 'status', displayName: 'Status' },
   
  ];

  type TableData = {
    id: string;
    Partners: string;
    Offer: React.ReactNode;
    Category: React.ReactNode;
    Location: string;
    status: string;
    fetches:any;
    all?: any;
    
  };
  /*
    type TableData = {
    id: string;
    num: React.ReactNode;
    date: string;

    complainant: string;

    reason: string;

    action?: React.ReactNode;
  };*/

  const products: TableData[] = List;
  return (
    <>
      {/* <AppModal
       show={appModal.isOpen}
        onClose={appModal.close}
        onOpen={appModal.open}
        // title={"Deactivate Vendor"}
        // paragraph={"deactivate this vendor."}
        // buttonText={"Deactivate Vendor"}
      /> */}
      <div className="disputes">
       

      
        <Table<TableData>
          fields={tableHead}
          tableData={products}
          checked={true}
          fetches={()=>fetchdata()}
          onClick={() => history.push('/app/business/discount-details')}
          builder={(field, data) => {
            switch (field.name) {
              
              default:
                return data[field.name];
            }
          }}
        />
      </div>
    </>
  );
};

export default DiscountPartnersTable;
