import React,{useEffect, useState} from 'react';

import { UpIconCards, DownIconCards } from '../Cards/Cards';
import Bag1 from '../../assets/icons/Bag1.svg';
import Bag2 from '../../assets/icons/Bag2.svg';

import Wallet from '../../assets/icons/Wallet.svg';
import Users from '../../assets/icons/addUsers.svg';
import './cards.scss';

const CardComponent = () => {
  const [wallets, setWallet]=useState<any>("");
  useEffect(()=>{
    (async () => {
      const url_string = window.location.href;
      const url = new URL(url_string);
      const walletss = url.searchParams.get("wallet");
      setWallet(walletss); 
    })();
   
  }
 
  );

  return (
    <div className="card-container">
      <div className="rows p-3 ">
        <div className="col">
          <UpIconCards
            bgColor="#fff"
            textColor="rgba(4, 88, 96, 1)"
            icon={Bag2}
            title="Products"
            amount={10}
            borderColor="1px solid #C4C4C4"
            numberColor="rgba(4, 88, 96, 1)"
          />
        </div>
        <div className="col">
          <UpIconCards
            bgColor="#fff"
            textColor="rgba(255, 127, 0, 1)"
            icon={Bag1}
            title="Unavailable Products"
            amount={20}
            borderColor="1px solid #C4C4C4"
            numberColor="rgba(4, 88, 96, 1)"
          />
        </div>
        <div className="col">
          <DownIconCards
            bgColor="#fff"
            textColor="rgba(4, 88, 96, 1)"
            icon={Users}
            title="Sales Rep"
            amount={5}
            borderColor="1px solid #C4C4C4"
            numberColor="rgba(4, 88, 96, 1)"
          />
        </div>
        <div className="col">
          <UpIconCards
            bgColor="#fff"
            textColor="rgba(4, 88, 96, 1)"
            icon={Wallet}
            title="Wallet"
            amount={"NGN"+wallets}
            borderColor="1px solid #C4C4C4"
            numberColor="rgba(4, 88, 96, 1)"
          />
        </div>
      </div>
    </div>
  );
};

export default CardComponent;
